import React from 'react';
import './App.css';
import { QueryClient, QueryClientProvider } from 'react-query';
import MemeList from './MemeList';
import { Sidebar, Menu, MenuItem, ProSidebarProvider } from 'react-pro-sidebar';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
const queryClient = new QueryClient();
function App() {
  return (
    <ProSidebarProvider>
      <QueryClientProvider client={queryClient}>
        <div className="App">
          <Router>
            <div id="header">
              <Sidebar>
                <Menu>
                  <MenuItem routerLink={<Link to="/" />}> Info</MenuItem>
                  <MenuItem routerLink={<Link to="/meme" />}> App</MenuItem>
                </Menu>
              </Sidebar>
            </div>

            <Routes>
              <Route
                path="/meme"
                element={
                  <div>
                    <MemeList />
                  </div>
                }
              ></Route>
              <Route
                path="/"
                element={
                  <div>
                    <h1>GA01-Boostrap Project-19127368</h1>
                  </div>
                }
              ></Route>
            </Routes>
          </Router>
        </div>
      </QueryClientProvider>
    </ProSidebarProvider>
  );
}

export default App;
